<template>
  <main class="text-light" >
    <div class="container">
      <section class="py-5 text-center container">
        <div class="row py-lg-5">
          <div class="col-lg-6 col-md-8 mx-auto">
            <h1 class="fw-light">Discord Login</h1>
            <p class="lead text-muted">
              Conecta tu cuenta con nosotros para poder tener mas actualizaciones en el futuro!!!
            </p>
            <p>
              <div>
                <a @click="loginWithDiscord">Logeate con Discord</a>
              </div>
            </p>
          </div>
        </div>
      </section>
    </div>
  </main>
</template>
  
<script>
import axios from 'axios';
//Metodo para acceder al login medainte axios
export default {
  methods: { 
    loginWithDiscord() {
      axios.get('/auth')
        .then(response => {
          const redirectUrl = response.data.redirectUrl;
          window.location.href = redirectUrl;
        })
        .catch(error => {
          console.error(error);
        });
    },
  }
};
</script>